<template>
  <div class="PostList">
    <!-- 在数据未返回的时候，显示这个正在加载的 gif -->
    <div class="loading" v-if="isLoading">
      <img src="../assets/loading.gif" />
    </div>
    <!-- 代表我们的主题列表 -->
    <div class="posts" v-else>
      <ul>
        <li>
          <div class="toobar">
            <span>全部</span>
            <span @click="alert">精华</span>
            <span @click="alert">分享</span>
            <span @click="alert">问答</span>
            <span @click="alert">招聘</span>
          </div>
        </li>
        <li v-for="post in posts">
          <!-- 头像 -->
          <img :src="post.author.avatar_url" alt="" />
          <!-- 回复量/浏览量 -->
          <span>
            <span class="reply_count">{{ post.reply_count }}</span>
            /{{ post.visit_count }}
          </span>
          <!-- 帖子的分类 -->
          <span
            :class="[
              {
                put_good: post.good == true,
                put_top: post.top == true,
                'topiclist-tab': post.good != true && post.top != true
              }
            ]"
          >
            <span>
              {{ post | tabFormatter }}
            </span>
          </span>
          <!-- 标题 -->
          <router-link
            :to="{
              name: 'post_content',
              params: {
                id: post.id,
                name: post.author.loginname
              }
            }"
          >
            <span>
              {{ post.title }}
            </span>
          </router-link>

          <!-- 最终回复时间 -->
          <span class="last_reply">
            {{ post.last_reply_at | formatDate }}
          </span>
        </li>
        <li>
          <!-- 分页 -->
          <psgination @handleList="renderList"></psgination>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import psgination from "./Psgination";
export default {
  name: "PostList",
  data() {
    return {
      isLoading: false, //是否正在加载
      posts: [], //代表页面的列表数组
      postpage: 1
    };
  },
  components: {
    psgination
  },
  methods: {
    getData() {
      this.$http
        .get("https://cnodejs.org/api/v1/topics", {
          params: {
            page: this.postpage,
            limit: 20
          }
        })
        .then(result => {
          this.isLoading = false; //页面加载成功以后取消动画
          this.posts = result.data.data;
        })
        .catch(err => {
          //处理返回失败后的问题
          console.log(err);
        });
    },
    renderList(value) {
      this.postpage = value;
      this.getData();
    },
    alert() {
      alert(`由于该页与'全部'页面完全一致，暂时未做。。`);
    }
  },
  //页面载入前开始执行
  beforeMount() {
    this.isLoading = true; //加载成功之前显示加载动画
    this.getData(); //在页面加载之前获取数据
  }
};
</script>

<style scoped>

ul {
  padding: 0 10px 10px 10px;
}
.PostList {
  background-color: #e1e1e1;
}
.posts {
  padding-top: 15px;
}

.PostList img {
  height: 30px;
  width: 30px;
  vertical-align: middle;
}

ul {
  list-style: none;
  width: 90%;
  max-width: 1344px;
  margin: 0 auto;
}

ul li:not(:first-child) {
  padding: 9px;
  font-size: 15px;
  font-family: "Helvetica Neue", "Luxi Sans", "DejaVu Sans", Tahoma,
    "Hiragino Sans GB", STHeiti, sans-serif !important;
  font-weight: 400;
  background-color: white;
  color: #333;
  border-top: 1px solid #f0f0f0;
}

li:not(:first-child):hover {
  background: #f5f5f5;
}

li:last-child:hover {
  background: white;
}

li span {
  line-height: 30px;
}

.allcount {
  width: 70px;
  display: inline-block;
  text-align: center;
  font-size: 12px;
}

.reply_count {
  color: #9e78c0;
  font-size: 14px;
}

.put_good,
.put_top {
  background: #80bd01;
  padding: 2px 4px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -o-border-radius: 3px;
  color: #fff;
  font-size: 12px;
  margin-right: 10px;
}

.topiclist-tab {
  background-color: #e5e5e5;
  color: #999;
  padding: 2px 4px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -o-border-radius: 3px;
  font-size: 12px;
  margin-right: 10px;
}

.last_reply {
  text-align: right;
  min-width: 50px;
  display: inline-block;
  white-space: nowrap;
  float: right;
  color: #778087;
  font-size: 12px;
}

.toobar {
  height: 40px;
  background-color: #f5f5f5;
}

.toobar span {
  font-size: 14px;
  color: #80bd01;
  line-height: 40px;
  margin: 0 10px;
  cursor: pointer;
}

.toobar span:hover {
  color: #9e78c0;
}

a {
  text-decoration: none;
  color: black;
}

a:hover {
  text-decoration: underline;
}

/* .loading {
    text-align: center;
    padding-top: 300px;
  } */
</style>



