  <div id="app">
    <Header></Header>
    <div class="main">
       <router-view name='slidebar'></router-view>
      <router-view name="main"></router-view>
    </div>
    
  </div>
</template>

<script>
import Header from './components/Header'
import PostList from './components/PostList'

export default {
  name: 'App',
  components:{
    Header,
    PostList
  }
}
</script>

<style>
*{
  padding: 0;
  margin: 0;
}
.main{
  width: 100%;
  margin: 0 auto;
}
</style>



